html,body {
	height:100%;
}
body{
	background: #e5e3dd;
	margin: 0;
	font-family: 'ProximaNova-Bold', sans-serif;
}
img {
	width: 100%;
	height: auto;
}
p{
	margin: 0;
}
@font-face {
  font-family: 'ProximaNova-Bold';
  src: url('../font/ProximaNova-Bold.eot?#iefix') format('embedded-opentype'),  url('../font/ProximaNova-Bold.otf')  format('opentype'),
	   url('../font/ProximaNova-Bold.woff') format('woff'), url('../font/ProximaNova-Bold.ttf')  format('truetype'), url('../font/ProximaNova-Bold.svg#ProximaNova-Bold') format('svg');
  font-weight: normal;
  font-style: normal;
}
video{
	display: block;
	width: 100%;
}
#wrapper{
	width: 100%;
	max-width: 1280px;
	margin: 0 auto;
}
#title-wrap{
	clear: both;
    position: relative;
    video{
    	height: 100%;
    	width: 100%;
    }
	img{
	    position: absolute;
	    right: 0;
	    top: 0;
	    z-index: 2;
	    pointer-events:none;
	}
}
.padding-clear{
	
	background: #000;
}
#instruct {
	text-align: right;
	text-transform: uppercase;
    font-size: 23.8px;
    line-height: 25px;
	/*padding:0 53px;*/
	margin-top: 25px;
	label{
		margin-top: 31px;
		color: #fff;
		font-size: 30px;
		line-height: 39px;
		float: right;
		text-align: center;
		border-radius: 10px;
		background-color: rgb(22, 45, 132);
		width: 288px;
		padding:11px 0;
		letter-spacing: 2px;
		cursor: pointer;
		position: relative;
		&.disabled{
			background:rgba(22, 45, 132,0.3);
			cursor: no-drop;
			pointer-events: none;
		}
		input {
			cursor: inherit;
		    display: block;
		    font-size: 999px;
		    filter: alpha(opacity=0);
		    height: 100%;
		    width: 100%;
		    opacity: 0;
		    position: absolute;
		    right: 0;
		    text-align: right;
		    top: 0;
		}
	}
	progress{
		display: none;
	    position: absolute;
	    left: 0;
	    top: 100%;
	    width: 100%;
	}
	&:after{
		content:"";
		clear: both;
		display: table;
	}
}
#share {
    background: url(../img/share-bg.jpg) 16% 56px no-repeat #fffcf5;
    display: none;
    padding: 3.9% 4.4% 2.8% 29%;
    justify-content: space-between;
	> div {
		float: left;
		width: 24%;
		cursor: pointer;
	}
	&:after{
		content:"";
		clear: both;
		display: table;
	}
}
#title-treatment{
    width: 42%;
    height: 0;
    padding-bottom: 11%;
    background: url(../img/tt.png) no-repeat;
    background-size: 100%;
    float: right;
   	margin-top: 42px;
    margin-bottom: 16px;
}
.copy {
    text-align: right;
    font-size: 10px;
    color: #232323;
    font-family: 'arial';
    margin-top: 7px;
}